.config-page
{
    position: absolute;
    top: 17%;
    left: 12%;
    width: 81%;
    height: 100%;
}

.config-page .card-container
{
    margin-bottom: 2%;
    height: 37.24%;
    vertical-align: top;
}

.config-page .card-container:nth-child(2)
{
    margin-left: 2.3%;
    margin-right: 2.3%;
}

.config-page-btn
{
    position: absolute;
    color: #FFFFFF;
    border-radius: 6px;
    font-size: max(0.9vw, 12.96px);
    font-weight: 600;
    text-align: center;
    line-height: 185%;
    top: 110%;
    width: 14%;
    height: 3%;
    cursor: pointer;
}

.config-page-btn:hover
{
    filter: brightness(120%);
}

.config-page-btn#cancel
{
    background-color: #C6C6C6;
    left: 32%;
}

.config-page-btn#submit
{
    background-color: #005570;
    left: 48%;
}